.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: auto; /* Ajustar el ancho al contenido */
  max-width: 90%; /* Limitar el ancho máximo */
  max-height: 500px; /* Altura máxima para mostrar 10 filas */
  overflow: hidden;
}

.modal-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
  max-height: 200px; /* Altura máxima para el área de scroll */
  overflow-y: auto; /* Habilitar scroll vertical si hay más de 10 filas */
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.modal-grid-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.modal-grid-item button {
  margin-left: 10px;
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.modal-grid-item button:hover {
  background-color: #0056b3;
}
